import React, { useState } from "react";
import logo from "../Images/logo.svg";
import axios from "axios";
import LoadingButton from "@mui/lab/LoadingButton";
import VpnKeyIcon from "@mui/icons-material/VpnKey";
function Login(props) {
  const [username, setusername] = useState("");
  const [password, setpassword] = useState("");
  const [loading, setloading] = useState(false);
  const [message, setmessage] = useState("");
  const login = () => {
    setloading(true);
    if (username.trim() === "" || password.trim() === "") {
      setmessage("erreur un champ vide");
      setloading(false);
    } else {
      setmessage("");
      axios
        .post("http://localhost:3001/dash/login", {
          username: username,
          password: password,
        })
        .then((res) => {
          if (res.data.auth) {
            localStorage.setItem("tkn", res.data.token);
            props.setuploader(!props.uploader);
          } else {
            setmessage(res.data.message);
          }
          setloading(false);
        })
        .catch((e) => {
          console.log(e);
          setloading(false);
          setmessage("erreur dans le serveur");
        });
    }
  };

  return (
    <div className="login">
      <div className="frm cint">
        <div className="info_frm">
          <img src={logo} alt="logo" className="logo" />

          <h1 className="ttl">Admin Dashboard</h1>
          <p>
            Cette Page c"est juste pour les co founders de Hss vous ne pouvez
            Pas Acceder
          </p>
          <div className="formu_frm">
            <div>
              <b>
                Nom D'utilisateur <span className="clmv ">*</span>
              </b>
              <input
                type="text"
                placeholder="Nom D'utilisateur"
                onChange={(e) => setusername(e.target.value)}
                value={username}
                onKeyDown={(e) => {
                  e.key === "Enter" ? login() : console.log("");
                }}
              />
            </div>
            <div>
              <b>
                Mot de Passe <span className="clmv ">*</span>
              </b>
              <input
                type="password"
                value={password}
                onChange={(e) => setpassword(e.target.value)}
                placeholder="Votre mot de pass"
                onKeyDown={(e) => {
                  e.key === "Enter" ? login() : console.log("");
                }}
              />
            </div>
            <div>
              <p>{message}</p>
            </div>
            <div>
              <LoadingButton
                className="btn-frm cint mv"
                loading={loading}
                variant="contained"
                loadingPosition="start"
                startIcon={<VpnKeyIcon />}
                onClick={() => {
                  login();
                }}
              >
                Connecter
              </LoadingButton>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}

export default Login;
