import { useEffect, useState, Fragment } from "react";

import Box from "@mui/material/Box";
import Stepper from "@mui/material/Stepper";
import Step from "@mui/material/Step";
import StepButton from "@mui/material/StepButton";
import Button from "@mui/material/Button";
import Typography from "@mui/material/Typography";
import Table from "@mui/material/Table";
import TableBody from "@mui/material/TableBody";
import TableCell from "@mui/material/TableCell";
import TableContainer from "@mui/material/TableContainer";
import TableRow from "@mui/material/TableRow";
import { InputAdornment, TextField } from "@mui/material";
import PlayArrowIcon from "@mui/icons-material/PlayArrow";
import LoadingButton from "@mui/lab/LoadingButton";
import Swal from "sweetalert2";
import LinearProgress from "@mui/material/LinearProgress";

import AccountCircleIcon from "@mui/icons-material/AccountCircle";
import PasswordIcon from "@mui/icons-material/Password";
import TitleIcon from "@mui/icons-material/Title";
import AlternateEmailIcon from "@mui/icons-material/AlternateEmail";
import axios from "axios";
const steps = ["Choix de thme", "Create the Site", "PreConfiguration"];

function SeconInfo(props) {
  const [activeStep, setActiveStep] = useState(0);
  const [completed, setCompleted] = useState({});
  const [title, settitle] = useState("");
  const [email, setemail] = useState("");
  const [loading, setloading] = useState(false);
  const totalSteps = () => {
    return steps.length;
  };

  const completedSteps = () => {
    return Object.keys(completed).length;
  };
  const start = () => {
    setloading(true);
    axios
      .post("http://localhost:3001/dash/launch/start", {
        id: props.client.id,
      })
      .then((res) => {
        setloading(false);
        //props.setreload(!props.reload);
      });
  };
  const isLastStep = () => {
    return activeStep === totalSteps() - 1;
  };

  const allStepsCompleted = () => {
    return completedSteps() === totalSteps();
  };

  const handleNext = () => {
    const newActiveStep =
      isLastStep() && !allStepsCompleted()
        ? // It's the last step, but not all steps have been completed,
          // find the first step that has been completed
          steps.findIndex((step, i) => !(i in completed))
        : activeStep + 1;
    setActiveStep(newActiveStep);
  };

  const handleBack = () => {
    setActiveStep((prevActiveStep) => prevActiveStep - 1);
  };

  const handleStep = (step) => () => {
    setActiveStep(step);
  };

  const handleComplete = () => {
    const newCompleted = completed;
    newCompleted[activeStep] = true;
    setCompleted(newCompleted);
    handleNext();
  };

  const handleReset = () => {
    setActiveStep(0);
    setCompleted({});
  };

  const preConf = () => {
    setloading(true);
    axios
      .post("http://localhost:3001/dash/launch/configure", {
        sitetitle: title,
        id: props.client.id,
        username: props.client.username,
        password: props.client.password,
        email: email,
      })
      .then((res) => {
        if (res.data.pre) {
          const a = new FormData();
          a.append("weblog_title", title);
          a.append("user_name", props.client.username);
          a.append("admin_password", props.client.password);
          a.append("admin_password2", props.client.password);
          a.append("admin_email", email);
          a.append("blog_public", 1);
          a.append("Submit", "Install WordPress");
          axios
            .post(
              "http://" +
                props.client.username +
                ".example1.test/wp-admin/install.php?step=2",
              a
            )
            .then((res) => {
              console.log("reloaded");
              props.setreload(!props.reload);
              setloading(false);
            })
            .catch((err) => {
              console.log("reloaded");
              props.setreload(!props.reload);
              setloading(false);
            });
        } else {
          Swal.fire("Oops", res.data.message, "error");
          setloading(false);
        }
      });
  };
  return (
    <div style={{ padding: "20px" }}>
      <Box sx={{ width: "100%" }}>
        <Stepper nonLinear activeStep={activeStep}>
          {steps.map((label, index) => (
            <Step key={index} completed={completed[index]}>
              <StepButton color="inherit" onClick={handleStep(index)}>
                {label}
              </StepButton>
            </Step>
          ))}
        </Stepper>
        <div>
          {allStepsCompleted() ? (
            <Fragment>
              <Typography sx={{ mt: 2, mb: 1 }}>
                All steps completed - you&apos;re finished
              </Typography>
              <Box sx={{ display: "flex", flexDirection: "row", pt: 2 }}>
                <Box sx={{ flex: "1 1 auto" }} />
                <Button onClick={handleReset}>Reset</Button>
              </Box>
            </Fragment>
          ) : (
            <Fragment>
              <Typography sx={{ mt: 2, mb: 1 }}>
                Step {activeStep + 1}
              </Typography>
              <Box sx={{ display: "flex", flexDirection: "row", pt: 2 }}>
                <Button
                  color="inherit"
                  disabled={activeStep === 0}
                  onClick={handleBack}
                  sx={{ mr: 1 }}
                >
                  Back
                </Button>
                <Box sx={{ flex: "1 1 auto" }} />
                <Button onClick={handleNext} sx={{ mr: 1 }}>
                  Next
                </Button>
                {activeStep !== steps.length &&
                  (completed[activeStep] ? (
                    <Typography
                      variant="caption"
                      sx={{ display: "inline-block" }}
                    >
                      Step {activeStep + 1} already completed
                    </Typography>
                  ) : (
                    <Button onClick={handleComplete}>
                      {completedSteps() === totalSteps() - 1
                        ? "Finish"
                        : "Complete Step"}
                    </Button>
                  ))}
              </Box>
            </Fragment>
          )}
        </div>
      </Box>
      {activeStep === 0 ? <div></div> : <></>}
      {activeStep === 1 ? (
        <div>
          {props.client.approuved ? (
            <h1>Site Bien Lance</h1>
          ) : (
            <LoadingButton
              className="btn-frm cint start"
              variant="contained"
              loadingPosition="start"
              startIcon={<PlayArrowIcon />}
              onClick={() => start()}
              loading={loading}
            >
              Start
            </LoadingButton>
          )}
        </div>
      ) : (
        <></>
      )}
      {activeStep === 2 ? (
        <TableContainer>
          <Table aria-label="simple table">
            <TableBody>
              <TableRow
                key="0"
                sx={{
                  "&:last-child td, &:last-child th": {
                    border: 0,
                  },
                }}
              >
                <TableCell>
                  <TextField
                    id="input-with-icon-textfield"
                    label="Titre Du Site"
                    value={title}
                    onChange={(e) => settitle(e.target.value)}
                    InputProps={{
                      startAdornment: (
                        <InputAdornment position="start">
                          <TitleIcon />
                        </InputAdornment>
                      ),
                    }}
                    variant="standard"
                  />
                </TableCell>
              </TableRow>
              <TableRow
                key="1"
                sx={{
                  "&:last-child td, &:last-child th": {
                    border: 0,
                  },
                }}
              >
                <TableCell>
                  <TextField
                    id="input-with-icon-textfield"
                    label="email"
                    value={email}
                    onChange={(e) => setemail(e.target.value)}
                    InputProps={{
                      startAdornment: (
                        <InputAdornment position="start">
                          <AlternateEmailIcon />
                        </InputAdornment>
                      ),
                    }}
                    variant="standard"
                  />
                </TableCell>
              </TableRow>
              <TableRow
                key="2"
                sx={{
                  "&:last-child td, &:last-child th": {
                    border: 0,
                  },
                }}
              >
                <TableCell>
                  <TextField
                    id="input-with-icon-textfield"
                    label="Username"
                    value={props.client.username}
                    InputProps={{
                      startAdornment: (
                        <InputAdornment position="start">
                          <AccountCircleIcon />
                        </InputAdornment>
                      ),
                    }}
                    variant="standard"
                  />
                </TableCell>
              </TableRow>
              <TableRow
                key="3"
                sx={{
                  "&:last-child td, &:last-child th": {
                    border: 0,
                  },
                }}
              >
                <TableCell>
                  <TextField
                    id="input-with-icon-textfield"
                    label="password"
                    value={props.client.password}
                    InputProps={{
                      startAdornment: (
                        <InputAdornment position="start">
                          <PasswordIcon />
                        </InputAdornment>
                      ),
                    }}
                    variant="standard"
                  />
                </TableCell>
              </TableRow>

              <TableRow
                key="4"
                sx={{
                  "&:last-child td, &:last-child th": {
                    border: 0,
                  },
                }}
              >
                <TableCell>
                  <Button onClick={() => preConf()} sx={{ mr: 1 }}>
                    Terminer
                  </Button>
                </TableCell>
              </TableRow>
            </TableBody>
          </Table>
        </TableContainer>
      ) : (
        <></>
      )}
      {loading ? <LinearProgress /> : <></>}
    </div>
  );
}

export default SeconInfo;
