import axios from "axios";
import React, { useEffect, useState } from "react";

import { Avatar, Badge } from "@mui/material";

function ClientList(props) {
  const [clients, setclients] = useState([]);

  function stringAvatar(name) {
    return {
      sx: {
        bgcolor: "#ff840d",
      },
      children: `${name.split(" ")[0][0]}${name.split(" ")[1][0]}`,
    };
  }
  useEffect(() => {
    console.log("get list");
    axios
      .get("http://localhost:3001/dash/client", {
        headers: { "x-access-token": localStorage.getItem("tkn") },
      })
      .then((res) => {
        if (res.data.auth) {
          setclients(res.data.data);
        } else {
          console.log(res.data.message);
        }
      })
      .catch((err) => {
        console.log(err);
      });
  }, [props.reload]);

  return (
    <div>
      {clients.length === 0 ? (
        <div>
          <b>no client</b>
        </div>
      ) : (
        clients.map((el) => (
          <div
            className={
              props.client.id === el.id
                ? "client_element active"
                : "client_element"
            }
            onClick={() => props.setclient(el)}
          >
            <div className="client_avatar">
              {el.approuved ? (
                <Badge
                  color="success"
                  overlap="circular"
                  badgeContent=" "
                  variant="dot"
                >
                  <Avatar {...stringAvatar(el.name)} />
                </Badge>
              ) : (
                <Avatar {...stringAvatar(el.name)} />
              )}
            </div>
            <div className="client_info">
              <p>{el.name}</p>
              <p>{el.created_at}</p>
            </div>
          </div>
        ))
      )}
    </div>
  );
}

export default ClientList;
