import { useEffect, useState } from "react";

import { styled } from "@mui/material/styles";
import Box from "@mui/material/Box";
import Paper from "@mui/material/Paper";
import Grid from "@mui/material/Grid";
import { Avatar, Badge } from "@mui/material";
import Table from "@mui/material/Table";
import TableBody from "@mui/material/TableBody";
import TableCell from "@mui/material/TableCell";
import TableContainer from "@mui/material/TableContainer";
import TableRow from "@mui/material/TableRow";
import PlayArrowIcon from "@mui/icons-material/PlayArrow";
import LoadingButton from "@mui/lab/LoadingButton";
import StopIcon from "@mui/icons-material/Stop";
import axios from "axios";
import SeconInfo from "./SeconInfo";
import Configured from "./Configured";

function Info(props) {
  const Item = styled(Paper)(({ theme }) => ({
    ...theme.typography.body2,
    color: theme.palette.text.secondary,
  }));
  const daa = (a) => {
    var year = new Date(a).getFullYear();
    var month = new Date(a).getMonth();
    var day = new Date(a).getDate();
    var hours = new Date(a).getHours();
    var min = new Date(a).getMinutes();
    const monthNames = [
      "January",
      "February",
      "March",
      "April",
      "May",
      "June",
      "July",
      "August",
      "September",
      "October",
      "November",
      "December",
    ];

    return ("0" + day).slice(-2) + " " + monthNames[month] + " " + year;
  };
  const dat = (a) => {
    var hours = new Date(a).getHours();
    var min = new Date(a).getMinutes();
    var sec = new Date(a).getSeconds();
    return (
      ("0" + hours).slice(-2) +
      ":" +
      ("0" + min).slice(-2) +
      ":" +
      ("0" + sec).slice(-2)
    );
  };
  const [loading, setloading] = useState(false);
  const [client, setclient] = useState({});
  const [reload, setreload] = useState(true);
  useEffect(() => {
    setloading(true);
    axios
      .post(
        "http://localhost:3001/dash/client/get",
        { id: props.client.id },
        {
          headers: { "x-access-token": localStorage.getItem("tkn") },
        }
      )
      .then((res) => {
        if (res.data.auth) {
          setclient(res.data.data[0]);
        }
        setloading(false);
      });
  }, [props.client.id, reload]);

  function stringAvatar(name) {
    return {
      sx: {
        bgcolor: "#ff840d",
        width: 100,
        height: 100,
        size: 50,
      },
      children: `${name.split(" ")[0][0]}${name.split(" ")[1][0]}`,
    };
  }
  const start = () => {
    setloading(true);
    axios
      .post("http://localhost:3001/dash/launch/start", {
        id: client.id,
      })
      .then((res) => {
        console.log(res.data);
        setreload((p) => !p);
        setloading(false);
      });
  };
  const stop = () => {
    setloading(true);
    axios
      .post("http://localhost:3001/dash/launch/stop", {
        id: client.id,
      })
      .then((res) => {
        console.log(res.data);
        setreload((p) => !p);
        setloading(false);
      });
  };

  return (
    <div>
      {client?.name ? (
        <Box sx={{ flexGrow: 1 }}>
          <Grid container spacing={2}>
            <Grid item xs={4}>
              <Item key="5" elevation="5">
                <div className="hmp">
                  <div className="head_info">
                    {client.approuved ? (
                      <Badge
                        color="success"
                        overlap="circular"
                        badgeContent=" "
                      >
                        <Avatar {...stringAvatar(client.name)} />
                      </Badge>
                    ) : (
                      <Avatar {...stringAvatar(client.name)} />
                    )}

                    <strong>{client.name}</strong>
                    <p>{client.type}</p>
                  </div>
                  <div className="head_second">
                    <TableContainer>
                      <Table aria-label="simple table">
                        <TableBody>
                          <TableRow
                            key="0"
                            sx={{
                              "&:last-child td, &:last-child th": {
                                border: 0,
                              },
                            }}
                          >
                            <TableCell component="th" scope="row">
                              Website Link
                            </TableCell>
                            <TableCell align="right">{client.name}</TableCell>
                          </TableRow>
                          <TableRow
                            key="1"
                            sx={{
                              "&:last-child td, &:last-child th": {
                                border: 0,
                              },
                            }}
                          >
                            <TableCell component="th" scope="row">
                              Type
                            </TableCell>
                            <TableCell align="right">{client.type}</TableCell>
                          </TableRow>
                          <TableRow
                            key="2"
                            sx={{
                              "&:last-child td, &:last-child th": {
                                border: 0,
                              },
                            }}
                          >
                            <TableCell component="th" scope="row">
                              Username
                            </TableCell>
                            <TableCell align="right">
                              {client.username}
                            </TableCell>
                          </TableRow>
                          <TableRow
                            key="3"
                            sx={{
                              "&:last-child td, &:last-child th": {
                                border: 0,
                              },
                            }}
                          >
                            <TableCell component="th" scope="row">
                              Password
                            </TableCell>
                            <TableCell align="right">
                              {client.password}
                            </TableCell>
                          </TableRow>
                          <TableRow
                            key="4"
                            sx={{
                              "&:last-child td, &:last-child th": {
                                border: 0,
                              },
                            }}
                          >
                            <TableCell component="th" scope="row">
                              Username
                            </TableCell>
                            <TableCell align="right">
                              {client.username}
                            </TableCell>
                          </TableRow>
                          <TableRow
                            key="5"
                            sx={{
                              "&:last-child td, &:last-child th": {
                                border: 0,
                              },
                            }}
                          >
                            <TableCell component="th" scope="row">
                              {client.approuved ? (
                                <div>
                                  {daa(client.last)}
                                  <br />
                                  {dat(client.last)}
                                </div>
                              ) : (
                                <strong>-</strong>
                              )}
                            </TableCell>
                            <TableCell align="right">
                              {client.approuved ? (
                                <LoadingButton
                                  className="btn-frm cint stop"
                                  variant="contained"
                                  loadingPosition="start"
                                  startIcon={<StopIcon />}
                                  loading={loading}
                                  onClick={() => stop()}
                                >
                                  Stop
                                </LoadingButton>
                              ) : (
                                <LoadingButton
                                  className="btn-frm cint start"
                                  variant="contained"
                                  loadingPosition="start"
                                  startIcon={<PlayArrowIcon />}
                                  onClick={() => start()}
                                  loading={loading}
                                >
                                  Start
                                </LoadingButton>
                              )}
                            </TableCell>
                          </TableRow>
                        </TableBody>
                      </Table>
                    </TableContainer>
                  </div>
                </div>
              </Item>
            </Grid>
            {client.approuved ? (
              <Grid item xs={8}>
                <Item key="5" elevation="5">
                  <div>
                    {client.sitetitle ? (
                      <Configured
                        client={client}
                        reload={reload}
                        setreload={(p) => setreload(p)}
                      />
                    ) : (
                      <SeconInfo
                        client={client}
                        reload={reload}
                        setreload={(p) => setreload(p)}
                      />
                    )}
                  </div>
                </Item>
              </Grid>
            ) : (
              <></>
            )}
          </Grid>
        </Box>
      ) : (
        <></>
      )}
    </div>
  );
}

export default Info;
