import React, { useEffect, useState } from "react";
import Table from "@mui/material/Table";
import TableBody from "@mui/material/TableBody";
import TableCell from "@mui/material/TableCell";
import TableContainer from "@mui/material/TableContainer";
import TableRow from "@mui/material/TableRow";
import axios from "axios";

function Configured(props) {
  const [token, settoken] = useState("-");
  useEffect(() => {
    axios
      .post(
        "http://localhost/" +
          props.client.username +
          "/wp-json/jwt-auth/v1/token",
        {
          username: props.client.username,
          password: props.client.password,
        }
      )
      .then((res) => {
        console.log(res.data);
        settoken(res.data.token);
      })
      .catch((err) => console.log(err));
  }, [props.client.id]);
  return (
    <div className="pds">
      <h2>Informations</h2>
      <TableContainer>
        <Table aria-label="simple table">
          <TableBody>
            <TableRow
              key="0"
              sx={{
                "&:last-child td, &:last-child th": {
                  border: 0,
                },
              }}
            >
              <TableCell component="th" scope="row">
                Website Link
              </TableCell>
              <TableCell align="right">
                <a
                  href={"http://" + props.client.username + ".example1.test"}
                  target="_blank"
                >
                  /{props.client.username}
                </a>
              </TableCell>
            </TableRow>
            <TableRow
              key="4"
              sx={{
                "&:last-child td, &:last-child th": {
                  border: 0,
                },
              }}
            >
              <TableCell component="th" scope="row">
                Admin Dashboard
              </TableCell>
              <TableCell align="right">
                <a
                  href={
                    "http://" +
                    props.client.username +
                    ".example1.test/wp-admin"
                  }
                  target="_blank"
                >
                  admin/{props.client.username}
                </a>
              </TableCell>
            </TableRow>
            <TableRow
              key="1"
              sx={{
                "&:last-child td, &:last-child th": {
                  border: 0,
                },
              }}
            >
              <TableCell component="th" scope="row">
                Type
              </TableCell>
              <TableCell align="right">{props.client.type}</TableCell>
            </TableRow>

            <TableRow
              key="2"
              sx={{
                "&:last-child td, &:last-child th": {
                  border: 0,
                },
              }}
            >
              <TableCell component="th" scope="row">
                Posts
              </TableCell>
              <TableCell align="right" className="elip"></TableCell>
            </TableRow>
            <TableRow
              key="3"
              sx={{
                "&:last-child td, &:last-child th": {
                  border: 0,
                },
              }}
            >
              <TableCell component="th" scope="row">
                Comments
              </TableCell>
              <TableCell align="right">{props.client.password}</TableCell>
            </TableRow>
          </TableBody>
        </Table>
      </TableContainer>
    </div>
  );
}

export default Configured;
