import "./App.css";

import Login from "./Components/Login";
import axios from "axios";
import { useEffect, useState } from "react";
import Load from "./Components/Load";
import Dash from "./Components/Dash";
import Swal from "sweetalert2";
function App() {
  const [user, setuser] = useState({});
  const [auth, setauth] = useState(false);
  const [loading, setloading] = useState(true);
  const [uploader, setuploader] = useState(false);
  useEffect(() => {
    setloading(true);
    axios
      .get("http://localhost:3001/dash/login/isAuth", {
        headers: { "x-access-token": localStorage.getItem("tkn") },
      })
      .then((res) => {
        if (res.data.auth) {
          setuser(res.data.user);
          setauth(true);
          Swal.fire({
            position: "top-end",
            icon: "success",
            title: `Bienvenue ${res.data.user?.name}`,
            showConfirmButton: false,
            timer: 1500,
          });
        } else {
          setauth(false);
          setuser({});
        }
        setloading(false);
      })
      .catch((e) => {
        console.log(e);
      });
  }, [uploader]);
  if (loading) {
    return <Load />;
  }
  return (
    <div className="App cint">
      {auth && user?.name ? (
        <Dash
          user={user}
          uploader={uploader}
          setuploader={(a) => setuploader(a)}
        />
      ) : (
        <Login uploader={uploader} setuploader={(a) => setuploader(a)} />
      )}
    </div>
  );
}

export default App;
