import LoadingButton from "@mui/lab/LoadingButton";
import React, { useEffect, useState } from "react";
import logo from "../Images/logo.svg";
import LockIcon from "@mui/icons-material/Lock";
import Swal from "sweetalert2";
import axios from "axios";
import Client from "./client/Client";
import { Button, Drawer, IconButton } from "@mui/material";
import Snackbar from "@mui/material/Snackbar";
import { Avatar } from "@mui/material";
import PersonRemoveIcon from "@mui/icons-material/PersonRemove";
import ListIcon from "@mui/icons-material/List";
import { io } from "socket.io-client";
import Info from "./info/Info";

function Dash(props) {
  const [loading, setloading] = useState(false);
  const [client, setclient] = useState({});
  const [reload, setreload] = useState(false);
  const [open, setopen] = useState(false);
  const [rtype, setrtype] = useState(false);
  const [rdt, setrdt] = useState({ r: false, type: "" });
  const [openSn, setopenSn] = useState(false);
  const [mesSnack, setmesSnack] = useState("");
  const [sock, setsock] = useState({});
  function stringAvatar(name) {
    return {
      sx: {
        bgcolor: "#ff840d",
      },
      children: `${name.split(" ")[0][0]}${name.split(" ")[1][0]}`,
    };
  }
  if (!props.user.name) {
    props.setuploader(!props.uploader);
  }
  const logout = () => {
    Swal.fire({
      title: "tes-vous sre  ?",
      text: "voulez-vous vous dconnecter ?",
      icon: "warning",
      showCancelButton: true,
      confirmButtonColor: "#3085d6",
      cancelButtonColor: "#d33",
      confirmButtonText: "Dconnecter",
    }).then(async (result) => {
      if (result.isConfirmed) {
        setloading(true);
        await localStorage.removeItem("tkn");
        await props.setuploader(!props.uploader);
        await setloading(false);
      }
    });
  };
  const reclient = (b) => {
    console.log(b);
    setclient(b);
  };
  const deleteClient = () => {
    Swal.fire({
      title: "tes-vous sre  ?",
      text: "voulez-vous vous supprimer " + client.name + " ?",
      icon: "warning",
      showCancelButton: true,
      confirmButtonColor: "#3085d6",
      cancelButtonColor: "#d33",
      confirmButtonText: "Supprimer",
    }).then(async (result) => {
      if (result.isConfirmed) {
        setloading(true);
        axios
          .post(
            "http://localhost:3001/dash/client/delete",
            {
              id: client.id,
            },
            {
              headers: { "x-access-token": localStorage.getItem("tkn") },
            }
          )
          .then((res) => {
            if (res.data.auth) {
              Swal.fire({
                position: "top-end",
                icon: "error",
                title: res.data.message,
                showConfirmButton: false,
                timer: 1500,
              });
            }
            if (res.data.client) {
              Swal.fire({
                position: "top-end",
                icon: "error",
                title: res.data.message,
                showConfirmButton: false,
                timer: 1500,
              });
              props.setreload(!props.reload);
              reload();
            } else {
              Swal.fire({
                position: "top-end",
                icon: "error",
                title: res.data.message,
                showConfirmButton: false,
                timer: 1500,
              });
            }
            setloading(false);
          })
          .catch((e) => {
            console.log(e);
            setloading(false);
          });
      }
    });
  };
  useEffect(() => {
    if (!client.id) {
      setopen(true);
    } else {
      setopen(false);
    }
  }, [client.id]);
  useEffect(() => {
    const id = props.user.id;
    const adm = "bar";
    const socket = io("http://localhost:3001", {
      query: { id: id },
      auth: { adm: "hamoudi" },
    });
    console.log("her is " + socket.id);
    setsock(socket);
    socket.on("typeAdd", (data) => {
      setmesSnack("client" + data.message);
      setrtype((p) => !p);
      setopenSn(true);
    });
    socket.on("typeRemove", (data) => {
      setmesSnack("client" + data.message);
      setrtype((p) => !p);
      setopenSn(true);
    });
    socket.on("fileAdd", (data) => {
      setmesSnack("client" + data.message);
      setrdt((p) => ({ r: !p.r, type: data.type }));
      setopenSn(true);
    });
    socket.on("fileRemove", (data) => {
      setmesSnack("client" + data.message);
      setrdt((p) => ({ r: !p.r, type: data.type }));
      setopenSn(true);
    });
  }, [props.user]);

  const handleClose = (event, reason) => {
    if (reason === "clickaway") {
      return;
    }

    setopenSn(false);
  };
  const action = (
    <React.Fragment>
      <Button color="secondary" size="small" onClick={handleClose}>
        UNDO
      </Button>
      <IconButton
        size="small"
        aria-label="close"
        color="inherit"
        onClick={handleClose}
      ></IconButton>
    </React.Fragment>
  );
  return (
    <div className="dash">
      <div className="dash_nav">
        <img src={logo} alt="logo" />

        <h1>{props.user.name}</h1>
        <LoadingButton
          className="btn-frm cint mv"
          loading={loading}
          variant="contained"
          loadingPosition="start"
          startIcon={<LockIcon />}
          onClick={() => logout()}
        >
          Logout
        </LoadingButton>
        <Snackbar
          open={openSn}
          autoHideDuration={6000}
          onClose={handleClose}
          message={mesSnack}
          action={action}
        />
      </div>
      <div className="archive">
        {client.id ? (
          <div className="aloa">
            <LoadingButton
              className="btn-frm cint mv"
              loading={loading}
              variant="contained"
              loadingPosition="start"
              startIcon={<ListIcon />}
              onClick={() => setopen(!open)}
            >
              Clients
            </LoadingButton>

            <div className="alop">
              <div className="sclient_user_info">
                <div className="client_avatar">
                  <Avatar {...stringAvatar(client.name)} />
                </div>
                <div className="client_info">
                  <p>{client.name}</p>
                  <p>{client.created_at}</p>
                </div>
              </div>
              <div className="account_info">
                <p>username : {client.username}</p>
                <p>password : {client.password}</p>
              </div>
            </div>
            <LoadingButton
              className="btn-frm cint mv"
              loading={loading}
              variant="contained"
              loadingPosition="start"
              onClick={() => {
                deleteClient();
              }}
              startIcon={<PersonRemoveIcon />}
            >
              Supprimer
            </LoadingButton>
          </div>
        ) : (
          <div className="aloa">
            <LoadingButton
              className="btn-frm cint mv"
              loading={loading}
              variant="contained"
              loadingPosition="start"
              startIcon={<ListIcon />}
              onClick={() => setopen(!open)}
            >
              Clients
            </LoadingButton>
          </div>
        )}
        <Drawer anchor="left" open={open} onClose={() => setopen(false)}>
          <Client
            setclient={(a) => setclient(a)}
            reload={reload}
            client={client}
          />
        </Drawer>
        <Info
          client={client}
          setreload={() => setreload()}
          reload={reload}
          socket={sock}
          rtype={rtype}
          rdt={rdt}
        />
      </div>
    </div>
  );
}

export default Dash;
