import React, { useEffect, useState } from "react";
import axios from "axios";
import LoadingButton from "@mui/lab/LoadingButton";
import VpnKeyIcon from "@mui/icons-material/VpnKey";
import AddCircleIcon from "@mui/icons-material/AddCircle";
import Swal from "sweetalert2";
import CreatableSelect from "react-select/creatable";

function AddClient() {
  const [name, setname] = useState("");
  const [username, setusername] = useState("");
  const [loading, setloading] = useState(false);
  const [password, setpassword] = useState("");
  const [type, settype] = useState("");
  const [types, settypes] = useState([]);
  function makeid(length) {
    var result = "";
    var characters =
      "ABCDEFGHIJKLMNOPQRSTUVWXYZabcdefghijklmnopqrstuvwxyz0123456789";
    var charactersLength = characters.length;
    for (var i = 0; i < length; i++) {
      result += characters.charAt(Math.floor(Math.random() * charactersLength));
    }
    return result;
  }

  const handleChange = (newValue, actionMeta) => {
    if (newValue.value) {
      settype(newValue.value);
    } else {
      settype("");
    }
  };
  const handleInputChange = (inputValue, actionMeta) => {};
  const generate = () => {
    if (name.trim() === "") {
      alert("name empty");
    } else {
      var a = name.split(" ");
      setusername(a[0][0] + a[1]);
      setpassword(makeid(6));
    }
  };
  const add = async () => {
    setloading(true);
    axios
      .post(
        "http://localhost:3001/dash/client/add",
        { name: name, username: username, password: password, type: type },
        {
          headers: { "x-access-token": localStorage.getItem("tkn") },
        }
      )
      .then((res) => {
        console.log(res.data);
        if (res.data.auth) {
          Swal.fire({
            position: "top-end",
            icon: "success",
            title: `Client ${name} Bien Ajoute`,
            showConfirmButton: false,
            timer: 1500,
          });
        } else {
          Swal.fire({
            position: "top-end",
            icon: "error",
            title: res.data.message,
            showConfirmButton: false,
            timer: 1500,
          });
        }
        setloading(false);
        setusername("");
        setname("");
        setpassword("");
      })
      .catch((e) => {
        console.log(e);
      });
  };
  useEffect(() => {
    axios
      .get("http://localhost:3001/dash/client/types", {
        headers: { "x-access-token": localStorage.getItem("tkn") },
      })
      .then((res) => {
        if (res.data.auth) {
        }
        if (res.data.type) {
          settypes(formTypes(res.data.data));
        } else {
          console.log(res.data.message);
        }
      });
  }, []);
  const formTypes = (a) => {
    var b = a.map((el) => {
      return { value: el.type, label: el.type };
    });

    return b;
  };
  return (
    <div className="addClient">
      <div className="add_client_frm">
        <div className="formu_frm str">
          <b>Ajouter un Client</b>
          <div>
            <input
              type="text"
              value={name}
              onChange={(e) => {
                setname(e.target.value);
              }}
              placeholder="Nom du Client"
            />
          </div>
          <div style={{ marginTop: "10px" }}>
            <CreatableSelect
              options={types}
              isClearable
              onChange={handleChange}
              onInputChange={handleInputChange}
              className="forum_type"
              placeholder="type"
            />
          </div>
          <div>
            <input
              type="text"
              value={username}
              onChange={(e) => {
                setusername(e.target.value);
              }}
              placeholder="Nom d'utilisateur"
            />
          </div>
          <div>
            <input
              type="text"
              value={password}
              onChange={(e) => {
                setpassword(e.target.value);
              }}
              placeholder="mot de pass"
            />
          </div>
          <span style={{ marginTop: "15px" }}>
            <LoadingButton
              className="btn-frm cint mv"
              loading={loading}
              variant="contained"
              loadingPosition="start"
              onClick={() => add()}
              startIcon={<AddCircleIcon />}
            >
              Ajouter
            </LoadingButton>
            <LoadingButton
              className="btn-frm cint mvs"
              loading={loading}
              variant="contained"
              loadingPosition="start"
              startIcon={<VpnKeyIcon />}
              onClick={() => generate()}
            >
              Generer
            </LoadingButton>
          </span>
        </div>
      </div>
    </div>
  );
}

export default AddClient;
